#spaceship-svg,#earth-svg,#tile-svg{
    position: fixed;
    margin: 0px;
    padding: 0px;
}
#spaceship-svg{
    max-width: 30vw;
    top: 0vh !important;
    left: 0vw !important;
}
#earth-svg{
    min-height: 250vh !important;
    min-width: 250vw !important;
    top: -60vh;
    left: -45vw;
}
#tile-svg{
    width: 200vw;
    left: 0vw;
    top: 0vh;
}


:root{
    --speed-rotation: 10s;
    --speed-land: 20s;
    --speed-upper-cloud: 10s;
    --speed-lower-cloud: 15s;
}


#LandA,#LandB,#LandC,#LandD{
    animation: land var(--speed-land) linear infinite;
}
#LandB,#UpperCloudsB,#LowerCloudsB{
    animation-delay: 5s;
}
#LandC,#UpperCloudsC,#LowerCloudsC{
    animation-delay: 15s;
}
#LandD,#UpperCloudsD,#LowerCloudsD{
    animation-delay: 10s;
}
#UpperCloudsA,#UpperCloudsB,#UpperCloudsC,#UpperCloudsD{
    animation: clouds var(--speed-upper-cloud) linear infinite;
}
#LowerCloudsA,#LowerCloudsB,#LowerCloudsC,#LowerCloudsD{
    animation: clouds var(--speed-lower-cloud) linear infinite;
}
@keyframes land{
    0%{
        transform: translate(0vw,0vh);
        opacity: 0;
    }
    5%{
        opacity: 1;
    }
    95%{
        opacity: 1;
    }
    100%{
        transform: translate(-100vw,-100vh);
        opacity: 0;
    }
}
@keyframes clouds{
    0%{
        transform: translate(0vw,0vh);
        opacity: 0;
    }
    20%{
        opacity: .5;
    }
    80%{
        opacity: .5;
    }
    100%{
        transform: translate(-50vw,-100vh);
        opacity: 0;
    }
}


#LightF,#LightG{
    animation: light-flash 1.7s ease infinite;
}
#LightB,#LightC{
    animation: light-flash 1.7s ease infinite, solar var(--speed-rotation) ease-in-out infinite;
}
#LightD,#LightE{
    animation: light-flash-delay 1.7s ease infinite, solar var(--speed-rotation) ease-in-out infinite;
}

#LightF{
    animation-delay: 200ms;
}
#LightG{
    animation-delay: 100ms;
}

@keyframes light-flash{
    0%{
        opacity: 0;
    }
    45%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    55%{
        opacity: 0;
    }
    100%{
        opacity: 0;
    }
}
@keyframes light-flash-delay{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0;
    }
    55%{
        opacity: 1;
    }
    60%{
        opacity: 0;
    }
    100%{
        opacity: 0;
    }
}

#LightA{
    animation: light-flash-more 1.5s ease infinite;
}

@keyframes light-flash-more{
    0%{
        opacity: 0;
    }
    20%{
        opacity: 0;
    }
    25%{
        opacity: 1;
    }
    50%{
        opacity: 1;
    }
    55%{
        opacity: 0;
    }
    70%{
        opacity: 0;
    }
    75%{
        opacity: 1;
    }
    80%{
        opacity: 0;
    }
    100%{
        opacity: 0;
    }
}


#SolarA,#SolarB,#SolarC,#SolarD,#SolarBB,#SolarDD{
    animation: solar var(--speed-rotation) ease-in-out infinite;
}
#SolarA,#SolarC,#LightB{
    transform-origin: 58% 74%;
}
#SolarB,#LightC,#SolarD{
    transform-origin: 54% 70%;
}
#SolarBB,#SolarDD,#LightD,#LightE{
    transform-origin: 26% 42%;
}

@keyframes solar{
    0%{
        transform: rotate(0deg);
    }
    50%{
        transform: rotate(7deg);
    }
    100%{
        transform: rotate(0deg);
    }
}




#PodA1{
    animation: pod-a var(--speed-rotation) ease infinite;
}
@keyframes pod-a{
    0%{
        transform: translate(0px,0px);
    }
    50%{
        transform: translate(15px,-5px);
    }
    100%{
        transform: translate(0px,0px);
    }
}

